.list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 50px;
  }
  
  .item {
    padding: 20px;
    background-color: rgba(144, 165, 179, 0.693);
    border-radius: 7px;
  }
  
  .title {
    margin-bottom: 10px;
  }
  

  
  .notFound {
    font-size: 36px;
    text-align: center;
    margin: 100px auto;
    padding: 20px;
    width: 500px;
    border-radius: 7px;
    background-color: rgba(144, 165, 179, 0.693);
  }